<template>
  <div id="app">
    <div id="menu">
      <div id="menu-list">
        <router-link to="/">
        <div v-on:click="moveMenuBar(0)" id="link-top" class="menu-link">
          Top
        </div></router-link>
        <router-link to="/works">
        <div v-on:click="moveMenuBar(1)" id="link-works" class="menu-link">
          Works
        </div></router-link>
        <router-link to="/about">
        <div v-on:click="moveMenuBar(2)" id="link-about" class="menu-link">
          About
        </div></router-link>
      </div>
      <div :class="{'menu-bar-moving': is_move}" class="menu-bar" :style="{left:selected_num*100+'px'}"></div>
    </div>
    <transition name="fade" mode="out-in">
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      selected_num: 0,
      is_move: false
    }
  },
  methods: {
    moveMenuBar (n) {
      this.selected_num = n
      this.is_move = true
    }
  },
  computed: {
  },
  watch: {
  }
}
</script>

<style>
body {
  margin:0;
  overflow: hidden;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#menu {
  width: 100%;
  height: 60px;
  margin: 0;
  /*position: absolute;*/
  top:0;
  position: relative;
  background-color: rgb(70, 160, 255);
  box-shadow: 0px 0px 3px -1px black;
}

#menu-list {
  height: 100%;
  display: flex;
  list-style: none;
}

.menu-link {
  height: 100%;
  width: 60px;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color:white;
  transition: all 0.5s ease;
  background-color: inherit;
}

.menu-link:hover {
  background-color: rgba(82, 82, 82, 0.3);
}

.menu-bar {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100px;
  height: 5px;
  background-color: white;
  transition: all .7s ease-in-out;
}

.menu-bar-moving {
  /*transition: all ease;*/
  animation: menu-bar-move-anim .7s ease;
}

@keyframes menu-bar-move-anim {
  0%{
    border-radius: 0;
  }
  50%{
    border-radius: 10px;
  }
  100%{
    border-radius: 0;
  }
}

.fade-leave {
  opacity: 1;
}
.fade-leave-active {
  opacity: 0;
  transform: translateX(-10px);
  transition-property: all;
  transition-timing-function: ease;
  transition-duration: .25s;
}
.fade-leave-to {

}

.fade-enter{
  opacity: 0;
  transform: translateX(10px);
}
.fade-enter-active {
  transition-property: all;
  transition-timing-function: ease;
  transition-duration: .25s;
  /*transition-delay: .25s;*/
}
.fade-enter-to {

}

a {
  text-decoration: none;
}


</style>



