<template lang="html">
  <div id="works">
    <h1>Works</h1>
    <transition-group tag="div" class="works-wrap" name="group" appear>
    <div v-for="work in works" class="work" :key="work.id"></div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: 'Works',
  data () {
    return {
      works: [
        { id: 0,
          msg: 'A' },
        { id: 1,
          msg: 'B' },
        { id: 2,
          msg: 'C' }
      ]
    }
  }
}
</script>

<style scoped lang="css">

.works-wrap {
  display: flex;
  width: 90%;
  /*height: 10vw;*/
  margin: auto;
  border-radius: 3px;
  background-color: rgba(0, 168, 213, 0.14);
}

.work {
  margin: 15px;
  width: 100px;
  height: 100px;
  background-color: rgba(28, 28, 28, 0.2);
}

.list-complete-item {
  transition: all 1s;
  display: inline-block;
  margin-right: 10px;
}
.list-complete-enter, .list-complete-leave-to
/* .list-complete-leave-active for below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
.list-complete-leave-active {
  position: absolute;
}

</style>



